@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-color: #2563eb;
  --accent-color: #10b981;
  --background-color: #f9fafb;
  --text-color: #111827;
}

body {
  background: var(--background-color);
  color: var(--text-color);
  margin: 0;
}

.sidebar {
  background: var(--primary-color);
}

.button {
  background: var(--accent-color);
}